// MODAL STARTS HERE //

.bg-modal {
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  display: none;
  justify-content: center;
  align-items: center;
}

.modal-contents {
  height: 300px;
  width: 500px;
  background-color: white;
  text-align: center;
  padding: 20px;
  position: relative;
  border-radius: 4px;
}

.close {
  position: absolute;
  top: 0;
  right: 10px;
  font-size: 42px;
  color: #333;
  transform: rotate(45deg);
  cursor: pointer;
  &:hover {
    color: #666;
  }
}

/* all */
::-webkit-input-placeholder {
  /* Webkit */
  color: $white-font;
}

:-moz-placeholder {
  /* Firefox 18-4 */
  color: $white-font;
}

::-moz-placeholder {
  /* Firefox 19+ */
  color: $white-font;
}

:-ms-input-placeholder {
  /*  IE10+  */
  color: $white-font;
}

.form {
  align-self: center;

  @media #{$mq-425-up} {
    height: 100%;
  }

  &__group {
    width: 100%;
  }
}

.form__frame {
  display: grid;
  grid-template-columns: 30% 1fr;
  grid-gap: 30px;
  align-items: center;
}

input {
  width: 100%;
  height: 42px;
  background-color: $dark-blue;
  border: none;
  margin-top: 20px;

  @media #{$mq-425-up} {
    height: 73px;
    margin-top: 0;
  }
}

input:required {
  box-shadow: none;
}

input:focus:required:invalid {
  border-bottom: 2px solid $alert;
}
input:focus:required:valid {
  border-bottom: 2px solid $success;
}

input[type='text'] {
  font-size: 1rem;
  color: $white-font;
  border-bottom: 2px solid $white;
}

input[type='email'] {
  font-size: 1rem;
  color: $white-font;
  border-bottom: 2px solid $white;
}

textarea {
  width: 100%;
  height: 123px;
  box-sizing: border-box;
  background-color: $dark-blue;
  color: $white-font;
  font-family: 'Fira Sans', sans-serif;
  font-size: 1rem;
  box-shadow: none;
  border: 2px solid $white;
  resize: none;

  @media #{$mq-425-up} {
    height: 240px;
  }
}

textarea::placeholder {
  color: white;
}

textarea:focus:valid {
  border-bottom: 3px solid $weird-green;
}

textarea:focus:required:invalid {
  border-bottom: 3px solid $alert;
}
